@import "colors.scss";
@import "media.scss";

body{
    background: $base;
}
.btn-primary{
    border-radius: 0;
    background: $primary-button-background;
    font-size: 1.1em;
    
    color: $primary-button-color;
    border: 1px solid $primary-button-background;
}
.btn-primary:hover{    
    background: $inverse-primary-button-background;
    color: $inverse-primary-button-color;
    border: 1px solid $inverse-primary-button-color;
}
.btn-primary-inverse{
    border-radius: 0;
    background: $inverse-primary-button-background;
    color: $inverse-primary-button-color;
    border: 1px solid $inverse-primary-button-color;
    font-size: 1.1em;
      
}
.btn-primary-inverse:hover{
    background: $primary-button-background;
    color: $primary-button-color;
    border: 1px solid $primary-button-color;
}
.btn-primary-transparent{
    border-radius: 0;
    font-size: 1.1em;
    
    background: transparent;
    border: 1px solid $primary-transparent-buttom-color;
    color: $primary-transparent-buttom-color;
}
.btn-primary-transparent:hover{
    background: transparent;
    border: 1px solid $primary-button-background;
    color: $primary-button-background;
}
.btn-lg{
    font-size: 1.3em;
    padding: 16px 70px;
}
.btn-md{
    font-size: 1.1em;
    padding: 8px 40px;
}
.btn-sm{
    font-size: 1.1em;
    padding: 8px 10px;
}
.btn-xs{
    font-size: 1em;
    padding: 5px 15px;
}
.hidden{
    display : none!important;
}
.title{
    text-align: center;
}
@media (min-width: $screen-sm-min) {
    .container {
    //width: 890px;
    padding-right: 0;
    padding-left: 0;
    
    }
}
@media (min-width: $screen-md-min) {
    .container {
        //width: 970px;
        
    }
    .btn-sm{
    font-size: 1.1em;
    padding: 8px 20px;
}
}
@media (min-width: $screen-lg-min) {
    .container {
        //width: 1170px;
    }
}